/*
    Manually Override CSS in components either by
    1. redefining existing classes
    2. adding new classes to brute-force change CSS
*/

/* New Class -- styles MaterialIcon Component */
.bottom-align {
    vertical-align: bottom;
    color: rgba(0, 0, 0, 0.65);
}

/* Restyles Ant Design Date Range Picker */
.ant-calendar-picker-input.ant-input {
    border: none;
    box-shadow: 0 2px 8px rgba(50, 60, 80, 0.15);
    -webkit-transition: box-shadow 0.3s;
    transition: box-shadow 0.3s;
    height: 33px;
    font-family: DIN;
}

.ant-calendar-picker-input.ant-input:hover {
    box-shadow: 0 2px 8px rgba(90, 46, 255, 0.25)
}
